.sidebar-wraper {
  max-width: 18.125rem;
}

.sidebar-wraper .sidebar-bg {
  width: 100%;
  background-color: #fcfdfd;
  padding: 1.5625rem 1.875rem;
  margin-bottom: 20px;
}

.sidebar-wraper .user-wraper {
  margin-bottom: 20px;
  width: 100%;
}

.sidebar-wraper .user-wraper .user-bg img {
  height: 100%;
  width: 100%;
}

.sidebar-wraper .user-wraper .user-data {
  position: relative;
  padding: 66px 26px 26px;
  background-color: #fff;
}

.sidebar-wraper .user-wraper .user-data img {
  position: absolute;
  top: 0%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.sidebar-wraper .user-wraper .user-data .main-user-name {
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center;
}

.sidebar-wraper .user-wraper .user-data .user-desc {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.75rem;
  font-weight: 400;
  max-width: 15rem;
  text-align: center;
  line-height: 120%;
}

.sidebar-wraper .new-article {
  width: 100%;
  background-color: #fff;
  padding: 20px 10px;
  margin-bottom: 20px;
}

.sidebar-wraper .new-article .article-btn {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 12px;
  line-height: 11px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  width: 100%;
  padding: 11px;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#0077b5), to(#0e6795));
  background: linear-gradient(180deg, #0077b5 0%, #0e6795 100%);
  border-radius: 4px;
  border: none;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.sidebar-wraper .new-article .article-btn:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  background-color: linear-gradient(180deg, #0077b5 0%, #185370 100%);
}

.sidebar-wraper .groups .flex-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #f4f4f4;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.sidebar-wraper .groups .flex-container .sidebar-edit {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.75rem;
  line-height: 11px;
  text-align: right;
  text-transform: uppercase;
  color: #0275b1;
}

.sidebar-wraper .groups .flex-group-item a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 10px;
  background-color: #fff;
  border-radius: 6px;
  border: 1px solid #f4f4f4;
  padding: 15px;
}

.sidebar-wraper .groups .flex-group-item a img {
  height: 52px;
  width: 52px;
  margin-right: 15px;
}

.sidebar-wraper .groups .flex-group-item a .group-name-desc {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  line-height: 125%;
  max-width: 9.375rem;
}

.sidebar-wraper .groups .all-group {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.75rem;
  line-height: 11px;
  text-transform: uppercase;
  color: #0275b1;
}

.sidebar-wraper .hashTags .sidebar-title {
  padding-bottom: 20px;
  border-bottom: 1px solid #f4f4f4;
  margin-bottom: 20px;
}

.sidebar-wraper .hashTags .flex-container-w {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.sidebar-wraper .hashTags .flex-container-w .hashtag-item {
  margin: 10px;
  padding: 7px;
  border-radius: 4px;
  background-color: #e9f0f8;
  text-align: center;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.sidebar-wraper .hashTags .flex-container-w .hashtag-item:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.sidebar-wraper .articles .sidebar-title {
  padding-bottom: 20px;
  border-bottom: 1px solid #f4f4f4;
  margin-bottom: 20px;
}

.sidebar-wraper .articles .article-list .article-item a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 15px;
}

.sidebar-wraper .articles .article-list .article-item a img {
  margin-right: 15px;
}

.sidebar-wraper .articles .article-list .article-item a .art-title {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 125%;
  color: #181818;
  margin-bottom: 5px;
}

.sidebar-wraper .articles .article-list .article-item a .viewers {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 10px;
  line-height: 150%;
  color: #181818;
  font-weight: bold;
}

.write-wraper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.637);
  -webkit-transform: translate(0);
          transform: translate(0);
  -webkit-transition: 0.4s;
  transition: 0.4s;
  z-index: 10;
}

.write-wraper .close-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  height: 15px;
  width: 15px;
  border: none;
  border-radius: 50%;
  background-color: rgba(194, 100, 100, 0.521);
  color: #fff;
  background-image: url(../../public/img/close-icon.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 60%;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.write-wraper .close-btn:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.write-wraper .article-form {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  background-color: #fff;
  padding: 15px 20px;
  border-radius: 5px;
}

.write-wraper .article-form .form-desc {
  padding: 25px 15px 10px;
  border-bottom: 1px solid #e9f0f8;
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1.25rem;
  font-weight: 600;
  color: #181818;
  text-transform: uppercase;
}

.write-wraper .article-form .art-area {
  resize: none;
  outline: none;
  display: block;
  min-width: 100%;
  margin: 15px 0;
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1.125rem;
  color: #181818;
}

.write-wraper .article-form .inp-wraper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: absolute;
  bottom: 35px;
  right: 30px;
  width: 150px;
}

.write-wraper .article-form .inp-f {
  position: relative;
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  overflow: hidden;
  cursor: pointer;
}

.write-wraper .article-form .inp-f:before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 150%;
  width: 150%;
  background-color: #fff;
  z-index: 5;
}

.write-wraper .article-form .inp-f::after {
  display: block;
  border-radius: 3px;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: 10;
}

.write-wraper .article-form .file::after {
  opacity: 0.1;
  background-image: url(../../public/img/paperclip.svg);
}

.write-wraper .article-form .img::after {
  opacity: 0.5;
  background-image: url(../../public/img/image.svg);
}

.write-wraper .article-form .video::after {
  opacity: 0.5;
  background-image: url(../../public/img/film.svg);
}

.write-wraper .article-form .snd {
  height: 30px;
  width: 30px;
  border: none;
  border-radius: 3px;
  background-size: 60%;
  background-position: center center;
  background-repeat: no-repeat;
  background-color: #0077b5;
  background-image: url(../../public/img/send.svg);
  cursor: pointer;
}

.hide-write {
  -webkit-transform: translate(-200%);
          transform: translate(-200%);
  opacity: 0;
}

@media screen and (max-width: 900px) {
  .sidebar-wraper .user-wraper,
  .sidebar-wraper .new-article,
  .sidebar-wraper .groups,
  .sidebar-wraper .hashTags,
  .sidebar-wraper .articles {
    display: none;
  }
}
