@import "functions.scss";
@import "varibles.scss";
.chat-wraper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  .chat-list {
    max-width: size(450);
    padding: 0;
    .chat-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: $white;
      padding: 15px 10px;
      border-bottom: 1px solid $porple100;
      cursor: pointer;
      transition: 0.4s;
      &:hover {
        transform: scale(1.01);
        background-color: $porple100;
      }
      img {
        height: 50px;
        width: 50px;
        border-radius: 50%;
        border: 3px solid $porple100;
      }
      .text-wraper {
        padding-left: 15px;
        .writer {
          font-family: $Ghotam;
          font-size: size(16);
          font-weight: 500;
          color: $black600;
          margin-bottom: 5px;
        }
        .writen-time {
          font-family: $Ghotam;
          font-size: size(14);
          font-weight: 400;
          color: $porple200;
          margin-bottom: 5px;
        }
      }
    }
    .active {
      transform: scale(1.01);
      background-color: $porple100;
    }
  }
  .chat-item-list {
    position: relative;
    background-color: $white;
    max-width: size(400);
    margin-left: 20px;
    .empt-text {
      font-family: $Ghotam;
      font-size: size(16);
      font-weight: 600;
      color: $black600;
      padding: 50px 15px;
      width: size(380);
      text-align: center;
    }
    .chat-title {
      font-family: $Ghotam;
      font-size: size(14);
      font-weight: 400;
      color: $blue100;
      margin-bottom: 5px;
      padding: 15px;
      border-bottom: 1px solid $porple100;
      width: 100%;
      text-align: center;
    }
    .msg-item {
      width: size(380);
      padding: 10px;

      .msg-text {
        font-family: $Ghotam;
        font-size: size(16);
        font-weight: 400;
        color: $black600;
        border-radius: 15px;
        padding: 5px;
        background-color: #cecece40;
        max-width: size(150);
      }
    }

    .left {
      margin-right: auto;
    }
    .right {
      margin-left: auto;
    }
    .write-msg {
      border-top: 2px solid$porple100;
      width: 100%;
      form {
        position: relative;
      }
      textarea {
        font-family: $Ghotam;
        font-size: size(16);
        font-weight: 500;
        color: $black600;
        border: none;
        outline: none;
        width: 100%;
        min-height: 50px;
        max-height: 200px;
        resize: none;
        transition: 0.4s;
        &:focus {
          border: 1px solid $porple200;
        }
      }
      .send-btn {
        position: absolute;
        top: 50%;
        right: 10px;
        height: 15px;
        width: 15px;
        transform: translateY(-50%);
        border: none;
        background: transparent;
        background-image: url(../../public/img/send-btn.png);
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        cursor: pointer;
      }
    }
  }
}
@media screen and (max-width: 900px) {
  .chat-wraper {
    width: 100%;
  }
}
@media screen and(max-width:680px) {
  .chat-wraper {
    position: relative;
    flex-direction: column;
    .chat-item-list {
      margin-top: 60px;
      max-width: 100%;
      .msg-item {
        width: 100%;
      }
    }
    .chat-list {
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 0;
      left: 15px;
      background-color: transparent;
      min-width: 100%;

      .chat-item {
        border-bottom: none;
        background-color: transparent;
        img {
          height: 40px;
          width: 40px;
        }
      }
      .text-wraper {
        display: none;
      }
    }
  }
}
