.chat-wraper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
}

.chat-wraper .chat-list {
  max-width: 28.125rem;
  padding: 0;
}

.chat-wraper .chat-list .chat-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #fff;
  padding: 15px 10px;
  border-bottom: 1px solid #cecece;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.chat-wraper .chat-list .chat-item:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: #cecece;
}

.chat-wraper .chat-list .chat-item img {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 3px solid #cecece;
}

.chat-wraper .chat-list .chat-item .text-wraper {
  padding-left: 15px;
}

.chat-wraper .chat-list .chat-item .text-wraper .writer {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #181818;
  margin-bottom: 5px;
}

.chat-wraper .chat-list .chat-item .text-wraper .writen-time {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #747474;
  margin-bottom: 5px;
}

.chat-wraper .chat-list .active {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  background-color: #cecece;
}

.chat-wraper .chat-item-list {
  position: relative;
  background-color: #fff;
  max-width: 25rem;
  margin-left: 20px;
}

.chat-wraper .chat-item-list .empt-text {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #181818;
  padding: 50px 15px;
  width: 23.75rem;
  text-align: center;
}

.chat-wraper .chat-item-list .chat-title {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  color: #0275b1;
  margin-bottom: 5px;
  padding: 15px;
  border-bottom: 1px solid #cecece;
  width: 100%;
  text-align: center;
}

.chat-wraper .chat-item-list .msg-item {
  width: 23.75rem;
  padding: 10px;
}

.chat-wraper .chat-item-list .msg-item .msg-text {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: #181818;
  border-radius: 15px;
  padding: 5px;
  background-color: #cecece40;
  max-width: 9.375rem;
}

.chat-wraper .chat-item-list .left {
  margin-right: auto;
}

.chat-wraper .chat-item-list .right {
  margin-left: auto;
}

.chat-wraper .chat-item-list .write-msg {
  border-top: 2px solid #cecece;
  width: 100%;
}

.chat-wraper .chat-item-list .write-msg form {
  position: relative;
}

.chat-wraper .chat-item-list .write-msg textarea {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #181818;
  border: none;
  outline: none;
  width: 100%;
  min-height: 50px;
  max-height: 200px;
  resize: none;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.chat-wraper .chat-item-list .write-msg textarea:focus {
  border: 1px solid #747474;
}

.chat-wraper .chat-item-list .write-msg .send-btn {
  position: absolute;
  top: 50%;
  right: 10px;
  height: 15px;
  width: 15px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: none;
  background: transparent;
  background-image: url(../../public/img/send-btn.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  cursor: pointer;
}

@media screen and (max-width: 900px) {
  .chat-wraper {
    width: 100%;
  }
}

@media screen and (max-width: 680px) {
  .chat-wraper {
    position: relative;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .chat-wraper .chat-item-list {
    margin-top: 60px;
    max-width: 100%;
  }
  .chat-wraper .chat-item-list .msg-item {
    width: 100%;
  }
  .chat-wraper .chat-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    position: absolute;
    top: 0;
    left: 15px;
    background-color: transparent;
    min-width: 100%;
  }
  .chat-wraper .chat-list .chat-item {
    border-bottom: none;
    background-color: transparent;
  }
  .chat-wraper .chat-list .chat-item img {
    height: 40px;
    width: 40px;
  }
  .chat-wraper .chat-list .text-wraper {
    display: none;
  }
}
