@import "functions.scss";
@import "varibles.scss";
.vacancy-wraper {
  border-radius: 5px;
  padding: 10px 15px;
  background-color: $white;
  .vacancy-item {
    position: relative;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    border: 1px solid $porple100;
    padding: 15px 10px;
    margin-bottom: 15px;
    cursor: pointer;
    transition: 0.4s;
    &:hover {
      background-color: $porple150;
    }
  }
  .unactive {
    opacity: 0.3;
  }
  .text-wraper {
    padding-left: 15px;
    .job-title {
      font-family: $Ghotam;
      font-size: size(18);
      font-weight: 600;
      color: $blue100;
      margin-bottom: 20px;
    }
    .vacancy-desc {
      font-family: $Ghotam;
      font-size: size(14);
      font-weight: 400;
      line-height: size(20);
      color: $black600;
      margin-bottom: 10px;
    }
    .publishing-date {
      font-family: $Ghotam;
      font-size: size(14);
      font-weight: 400;
      line-height: size(20);
      color: $porple100;
    }
  }
  .hide-btn {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    border: none;
    background-color: transparent;
    background-image: url(../../public/img/eye-off.svg);
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
    transition: 0.2s;
    &:active {
      transform: scale(0.8);
    }
  }
}
