.vacancy-wraper {
  border-radius: 5px;
  padding: 10px 15px;
  background-color: #fff;
}

.vacancy-wraper .vacancy-item {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-radius: 5px;
  border: 1px solid #cecece;
  padding: 15px 10px;
  margin-bottom: 15px;
  cursor: pointer;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.vacancy-wraper .vacancy-item:hover {
  background-color: #e9f0f8;
}

.vacancy-wraper .unactive {
  opacity: 0.3;
}

.vacancy-wraper .text-wraper {
  padding-left: 15px;
}

.vacancy-wraper .text-wraper .job-title {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 600;
  color: #0275b1;
  margin-bottom: 20px;
}

.vacancy-wraper .text-wraper .vacancy-desc {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.25rem;
  color: #181818;
  margin-bottom: 10px;
}

.vacancy-wraper .text-wraper .publishing-date {
  font-family: "Gotam", "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.25rem;
  color: #cecece;
}

.vacancy-wraper .hide-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  background-image: url(../../public/img/eye-off.svg);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.vacancy-wraper .hide-btn:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
